import Vue from 'vue';
import Router from 'vue-router';

// DefaultContainer
import DefaultContainer from './layouts/DefaultLayout.vue';

// Login
import Login from './views/Login.vue';

// dashboard
import Dashboard from './views/Dashboard.vue';

// scanner
import PocNewScan from './views/scanner/poc/PocNewScan'
import PocScans from './views/scanner/poc/PocScans'
import PocPluginList from './views/scanner/poc/PocPluginList'
import PocVulList from './views/scanner/poc/PocVulList'

import NessusNewScan from './views/scanner/nessus/NessusNewScan'

// Discovery & information gathering
import PortScanner from './views/discovery/port/PortScanner'

// Pentest tools
import XssPlatform from './views/tools/xss/XssPlatform'
import XssData from './views/tools/xss/XssData'
import HttpLog from './views/tools/httplog/HttpLog'
import JsonHijacker from './views/tools/jsonp/JsonHijacker'
import JsonHijackerData from './views/tools/jsonp/JsonHijackerData'

Vue.use(Router);

export default new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'open active',
  routes: [
    {
      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      component: DefaultContainer,
      children: [
        {
          path: '/',
          redirect: '/dashboard',
        },
        {
          path: '/dashboard',
          name: 'Dashboard',
          component: Dashboard,
        },
        // scanner
        {
          path: '/scanner/poc/new_scan',
          name: 'PocNewScan',
          component: PocNewScan,
        },
        {
          path: '/scanner/poc/scans',
          name: 'PocScans',
          component: PocScans,
        },
        {
          path: '/scanner/poc/plugin',
          name: 'PocPluginList',
          component: PocPluginList,
        },
        {
          path: '/scanner/poc/vuls',
          name: 'PocVulList',
          component: PocVulList,
        },
        {
          path: '/scanner/poc/vul/:tid',
          name: 'PocVulListFilter',
          component: PocVulList,
        },
        {
          path: '/scanner/nessus/new_scan',
          name: 'NessusNewScan',
          component: NessusNewScan,
        },
        // Tools
        {
          path: '/tools/xss',
          name: 'XssPlatform',
          component: XssPlatform,
        },
        {
          path: '/tools/xss/data',
          name: 'XssData',
          component: XssData,
        },
        {
          path: '/tools/httplog',
          name: 'HttpLog',
          component: HttpLog,
        },
        {
          path: '/tools/jsonp',
          name: 'JsonHijacker',
          component: JsonHijacker,
        },
        {
          path: '/tools/jsonp/data',
          name: 'JsonHijackerData',
          component: JsonHijackerData,
        },
        // Discovery
        {
          path: '/discovery/port',
          name: 'PortScanner',
          component: PortScanner,
        },
      ]
    },
    {
      path: '/login',
      name: "Login",
      component: Login,
    },
  ],
});
